import axios from 'axios'
import { useState, useCallback } from 'react'

const category = axios.create({baseURL: 'http//localhost:3334/api/category'})

function CardServer() {
    const [data, setData] = useState();
    const [name, setName] = useState();

    const getCardServer = useCallback(async () => {
        const {data} = await category.get('/')
        setData(data)
    }, [])

    const sendItem = useCallback(async () => {
        const response = await category.post('/', {name})
        console.log(response)
      }, [name])

    return (
        <>
        <div className="App">
      <button onClick={() => getCardServer()}>Получить данные</button>
      {data && data !== null && data.map((value) => (
        <p key={value.id}>{value.name}</p>))}
      <form>
        <input type='text' name={name} onChange={(event) => setName(event.target.value)} />
        <button type='button' onClick={() => sendItem()}> X </button>
      </form>
    </div>
    </>
    );

}

export default CardServer;


